import React, { useEffect, useState } from "react";
import axios from "axios";

// material ui components
import { Grid, Typography } from "@material-ui/core";

// required components
import Header from "../components/Header/Header";
import HeaderLinks from "../components/Header/HeaderLinks";
import Card from "../components/Card/Card";
import Button from "../components/CustomButtons/Button";
import FoodCart from "../components/FoodCart/FoodCart";

export default function ProductCatelog(props) {
  const [products, setProducts] = useState([]);
  const [moreItem, setMoreItem] = useState(false);

  useEffect(() => {
    axios
      .get("/api/product/get-products")
      .then(res => {
        setProducts(res.data.products);
        setMoreItem(res.data.moreItem);
      })
      .catch(err => console.log(err));
  }, []);

  const { ...rest } = props;

  let cols = [[]];
  let count = 0;

  for (let i = 0; i < Math.ceil(products.length / 3); i++) {
    for (let j = 0; j < 3; j++) {
      if (count >= products.length) break;
      else {
        cols[0].push(
          <Grid item lg={4}>
            <center>
              <Card product={products[count]} />
            </center>
          </Grid>
        );
        count++;
      }
    }
  }

  let rows = [];
  for (let i = 0; i < Math.ceil(products.length / 3); i++) {
    rows.push(
      <Grid container direction="row" lg={12} spacing={4}>
        {cols[i]}
      </Grid>
    );
  }

  const _handleViewMore = () => {
    const lastProduct = products[products.length - 1];
    axios
      .get(`/api/product/get-products/${lastProduct._id}`)
      .then(res => {
        setProducts(products.concat(res.data.products));
        setMoreItem(res.data.moreItem);
      })
      .catch(err => console.log(err));
  };

  return (
    <div>
      <Header
        brand="Product Catelog"
        rightLinks={<HeaderLinks from="component" />}
        fixed
        {...rest}
      />
      <FoodCart />
      <br />
      <br />
      <br />
      <br />
      {rows[0]}
      <br />
      <center>
        {moreItem !== false ? (
          <Button color="primary" onClick={_handleViewMore}>
            <Typography variant="subtitle1">View More</Typography>
          </Button>
        ) : null}
      </center>
    </div>
  );
}
