/*eslint-disable*/
import React, { useState } from "react";
import PropTypes from "prop-types";
// react components for routing our app without refresh
import { Link } from "react-router-dom";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";

// core components
import CustomDropdown from "../CustomDropdown/CustomDropdown.js";
import Button from "../CustomButtons/Button.js";
import Float from "../FloatingButton/Float";

// react and redux connection
import { connect } from "react-redux";

// import action for redux
import { openDialog } from "../../actions/product";

// styles
import styles from "../../assets/jss/user-kit/components/headerLinksStyle.js";

import { createMuiTheme } from "@material-ui/core/styles";

const theme = createMuiTheme();

const useStyles = makeStyles(styles);

function HeaderLinks(props) {
  const classes = useStyles();
  const [count, setCount] = useState(0);
  const [direction, setDirection] = useState("");

  const _addProduct = () => {
    props.openDialog();
  };

  return (
    <List className={classes.list}>
      {
        (window.onscroll = () => {
          setCount(window.pageYOffset);
          setDirection(window.oldScroll > window.scrollY ? "Up" : "Down");
          window.oldScroll = window.scrollY;
          if (window.scrollY == 0) setDirection("");
        })
      }
      {direction === "Down" ? (
        <Float direction="down" />
      ) : direction === "Up" ? (
        <Float direction="up" />
      ) : (
        ""
      )}
      <ListItem className={classes.listItem}>
        <Button
          color="transparent"
          className={classes.navLink}
          onClick={_addProduct}
        >
          ADD PRODUCT
        </Button>
      </ListItem>
    </List>
  );
}

HeaderLinks.propTypes = {
  openDialog: PropTypes.func.isRequired
};

export default connect(
  null,
  { openDialog }
)(HeaderLinks);
